.about_container{
    width:100%;max-width: 400px;margin: 0 auto;margin-top: 100px;
    padding:20px;border-radius: 5px;background-color: #fff;
    font-weight: 300;
    box-shadow: 0 13px 27px -5px rgba(50,50,93,0.25), 0 8px 16px -8px rgba(0,0,0,0.3), 0 -6px 16px -6px rgba(0,0,0,0.025);

}

.about_container span:first-child{font-size: 20px;}
.about_container span:last-child{display: block;margin-top: 10px;}